<template>
  <div class="happy-birthday" :style="{ 'backgroundColor': backgroundColor }">
    <h1 class="birthday-message">{{ birthdayMessage.substr(0, birthdayMessageLength) }}</h1>
    <div
      class="balloon"
      v-for="(balloon, i) in balloons" :key="i"
      :style="{ 'top': balloon.y + 'px', 'left': balloon.x + 'px' }">
      <img
        v-if="balloon.color === 0"
        src="../assets/green-balloon.png"/>
      <img
        v-if="balloon.color === 1"
        src="../assets/orange-balloon.png"/>
      <img
        v-if="balloon.color === 2"
        src="../assets/red-balloon.png"/>
      <img
        v-if="balloon.color === 3"
        src="../assets/yellow-balloon.png"/>
    </div>
    <div class="hacker-typer" v-if="hackerTyper">
      <div class="warning" v-if="!awoke">
        <h2>HALT: MODULE CORE HEMORRHAGE</h2>
        <p>Control has been yielded.</p>
        <p>Make your decisions accordingly.</p>
        <img src="../assets/error.gif"/>
        <button class="start-button" @click="awoke = true">Awaken</button>
      </div>
      <div class="warning" v-if="awoke">
        <h2>HALT: MODULE CORE HEMORRHAGE</h2>
        <p>If you continue, you better be sure of your decision. There is no going back.</p>
        <p>This website has been taken over.
          In order to get it back you must solve a series of puzzles.
          Each puzzle has a password to the next puzzle.
          If you want to read the original webpage message, you have no other choice.</p>
        <button class="start-button" @click="$router.push('/puzzle')">Begin</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'happy-birthday',
  data() {
    return {
      balloons: [],
      birthdayMessage: 'Happy Birthday Victoria!',
      birthdayMessageLength: 0,
      backgroundColor: 'white',
      hackerTyper: false,
      awoke: false,
    };
  },
  mounted() {
    const audio = new Audio('./Party-horn.mp3');
    audio.volume = 0.05;
    audio.play();
    const flashBackground = () => {
      this.backgroundColor = 'black';
      setTimeout(() => {
        this.backgroundColor = 'white';
      }, 100);
    };
    setTimeout(flashBackground, 4200);
    setTimeout(flashBackground, 4800);
    setTimeout(flashBackground, 5100);
    setTimeout(() => {
      this.backgroundColor = 'black';
      this.hackerTyper = true;
    }, 6400);
    const extendLength = () => {
      this.birthdayMessageLength += 1;
      if (this.birthdayMessageLength < this.birthdayMessage.length) {
        setTimeout(extendLength, 150);
      }
    };
    setTimeout(extendLength, 150);
    for (let i = 0; i < 30; i += 1) {
      this.balloons.push({
        color: Math.floor(Math.random() * 4),
        x: Math.random() * (window.innerWidth - 80),
        y: (window.innerHeight),
        speed: (Math.random() * 6) + 4,
      });
      const raiseBalloon = () => {
        this.balloons[i].y -= this.balloons[i].speed;
        if (this.balloons[i].y >= -120) {
          setTimeout(raiseBalloon, 20);
        }
      };
      setTimeout(raiseBalloon, 20);
    }
  },
};
</script>

