<template>
  <div class="puzzle">
    <nav>
      <img src="../assets/error.gif"/>
      <span class="title">{{ title }}</span>
      <div class="password">
        <label>Password:
          <input type="text" v-model="password"/>
        </label>
        <button class="submit-password-button" @click="onSubmit">Go</button>
      </div>
    </nav>
    <div class="description">
      <slot></slot>
    </div>
    <div class="hacker-typer" v-if="hackerTyper">
    </div>
  </div>
</template>

<script>
import md5 from 'md5';

export default {
  name: 'puzzle',
  props: {
    title: {
      type: String,
      required: true,
    },
    desiredPassword: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      hackerTyper: false,
      password: '',
    };
  },
  methods: {
    onSubmit() {
      if (this.desiredPassword === this.password) {
        this.$router.push(`/${md5(this.password)}`);
      } else {
        setTimeout(() => { this.hackerTyper = true; }, 100);
        setTimeout(() => { this.hackerTyper = false; }, 200);
        setTimeout(() => { this.hackerTyper = true; }, 300);
        setTimeout(() => { this.hackerTyper = false; }, 350);
        setTimeout(() => { this.hackerTyper = true; }, 400);
        setTimeout(() => { this.hackerTyper = false; }, 450);
      }
    },
  },
  mounted() {
    document.title = 'ERRRORRRRRRRR....';
    this.keydown = document.addEventListener('keydown', (event) => {
      if (event.key === 'Enter') {
        this.onSubmit();
      }
    });
  },
  destroyed() {
    document.removeEventListener('keydown', this.keydown);
  },
};
</script>

